html {
  font-size: 62.5%;
  box-sizing: border-box;
}
body {
  font: 1.6rem Helvetica;
  height: 100vh;
  margin: 0;
}
button {
  font-size: 1.6rem;
}
.full-width {
  width: 100%;
}
#root {
  height: 100%;
}
.grid-container {
  display: grid;
  grid-template-areas: 
  'header'
  'main'
  'footer';
  grid-template: 1fr;
  grid-template-rows: 5rem 1fr 5rem;
  height: 100%;
}
.header {
  grid-area: header;
  background-color: #203040;
  color:white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
}
.brand a {
  color: #fff;
  font-size: 2.5rem;
  font-weight: bold;
  text-decoration:none;
}
.header-links a {
  color:white;
  text-decoration:none;
}
.header-links a:hover {
  color: #ff8000;
}
.main {
  grid-area: main;
}
.footer {
  grid-area: footer;
  background-color: #203040;
  color:white;
  display:flex;
  justify-content: center;
  align-items:center;
}

/*  Home Screen  */
a {
  text-decoration: none;
}
a:hover  {
  color:#ff8000;
}
.products {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}
.products li {
  list-style: none;
  padding: 0;
  flex: 0 1 34rem;
  margin: 1rem;
  height: 50rem;
  border-bottom: .1rem #c0c0c0 solid;
}
.product-image {
  max-height: 34rem;
  max-width: 34rem;
}
.product {
  display:flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
}
.product-name {
font-size: 2rem;
font-weight: bold;
}
.product-brand {
font-size: 1.2rem;
color: #808080;
}
.product-price {
font-size: 2rem;
font-weight: bold;
}
.product-rating {
  margin-bottom: 1rem;
}
/* Rating */
.rating span {
  color: #ffb901;
  font-size: 1.8rem;
  margin: 0.1rem;
}
.rating span:last-child {
  color: #444444;
  font-size: 1.4rem;
}
a > .rating > span:last-child {
  color: #0000ff;
}
/* Sidebar */
.brand button {
  font-size: 3rem;
  padding:.5rem;
  background:none;
  border:none;
  color:#fff;
  cursor: pointer;
}
.sidebar {
  position:fixed;
  transition: all .5s ease;
  transform: translateX(-30rem);
  width:30rem;
  background:#f0f0f0;
  height: 100%;
}
.sidebar.open {
  transform:translateX(0);
}
.sidebar-close-button {
  border-radius: 50%;
  border: .1rem #000 solid;
  width: 3rem;
  height: 3rem;
  padding: .5rem;
  font-size: 2rem;
  padding-top:0;
  cursor: pointer;
  right: .5rem;
  top: 1.5rem;
  position: absolute;
}

/* Product details */
.details {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 1rem;
}
.detail-image {
  flex:2 1 60rem;
}
.detail-image img {
  max-width: 60rem;
  width:100% ;
}
.detail-info {
  flex: 1 1 30rem;
}
.detail-action {
  flex: 1 1 30rem;
}
.details-info ul, .details-action ul {
  list-style:none;
  padding:0;
}
.details-info li, .details-action li {
  margin-bottom: 1rem;
}
.back-to-result {
  padding: 1rem;
}
.details-action {
  border: .1rem #808080 solid;
  border-radius: 0.5rem;
  background-color: #f8f8f8;
  padding: 1rem;
}

.details-action ul li:last-child {
  display: flex;
  flex-direction: column;
}
.button {
  padding: 1rem;
  border: .1rem #808080 solid;
  border-radius: 0.5rem;
  cursor: pointer;

}
.button:hover {
  border: .1rem #404040 solid;
}
.button.primary {
  background:#d2c4a0;
}

.button.secondary {
  background-color: #f0f080;
}
.text-center {
  text-align: center;
}
/* cart */
.cart {
  display: flex;  
  flex-wrap: wrap;
  margin: 1rem;
  align-items: flex-start;
}
.cart-list {
  flex: 3 1 60rem;
}
.cart-action {
  flex: 1 1 20rem;
  background-color: #f8f8f8;
  border-color: #808080 solid;
  border-radius: .5rem;
  padding: 1rem;
}
.cart-list-container li {
  padding-bottom: 1rem;
  border-bottom: .1rem #c0c0c0 solid;
}
.cart-list-container {
  list-style-type: none;
  padding: 1rem;
}
.cart-list-container li {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
   
}
.cart-list-container li img {
  max-width: 10rem;
  max-height: 10rem;
  margin-bottom: 1rem;
  border: .1rem #808080 solid;

}
.cart-image {
  flex:1 1;
}
.cart-name {
  /* flex:8 1; */

}
.cart-price {
  flex: 1 1;
  font-size: 2.5rem;
  text-align: right;
}
/* Form */
.form {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.form-container {
  display: flex;
  flex-direction: column;
  width: 32rem;
  padding: 2rem;
  border: .1rem #c0c0c0 solid;
  border-radius: .5rem;
  list-style: none;
}
.form-container li {
  display: flex;;
  flex-direction: column;
  margin-bottom: 1rem;
  margin-top: 1rem;
}

input {
  padding: 1rem;
  border: .1rem #c0c0c0 solid;
  border-radius: .5rem;
  
}
/* Product */

.product-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.content-margined {
  margin: 1rem;
}
.table {
  width: 100%;
  border-collapse:collapse;
}
th {
  text-align: left;
}
.table td, .table td {
  text-align: left;
  border: 0.1rem solid #e4e4e4;
  padding: 0.5rem;
}
.table button {
  margin: 0 0.2rem;
}
tbody > tr:nth-child(odd) {
  background-color: #f4f4f4;
}
button {
  background-color: #f8f8f8;
  cursor:pointer;
}
button.small {
  font-size: 1.2rem;
}
input, textarea, button, select{
  padding: 1rem;
  border-radius: 0.5rem;
  border: 0.1rem #A4A4A4 solid;font-size: 1.6rem; font-family: Arial, Helvetica, sans-serif;
  --webkit-font-smoothing: antialiased;
  --webkit-moz-osx-font-smoothing: grayscale;
}
button.block {
  width: 100%;
}
input:hover, select:hover,textarea:hover, button:hover{
  border: 0.1rem #404040 solid;
}


/* checkout steps */
.checkout-steps {
  display: flex;
  justify-content: space-between;
  width: 40rem;
  margin: 1rem auto;
}
.checkout-steps > div {
  border-top: .3rem #c0c0c0 solid;
  color: #c0c0c0;
  flex:1 1;
  padding-top: 1rem;
}
.checkout-steps > div.active {
  border-top-color: #f08000 solid;
  color: #f08000;
}
/* place an order */
.placeorder {
  display: flex;
  flex-wrap: wrap;
  padding: 1rem;
  justify-content: space-between;
}
.placeorder-info {
  flex: 3 1 60rem;
}
.placeorder-action {
  flex: 1 1 20rem;
  border: 0.1rem #c0c0c0 solid;
  border-radius: 0.5rem;
  background-color: #fcfcfc;
  padding: 1rem;
}
.placeorder-info > div {
  border: 0.1rem #c0c0c0 solid;
  border-radius: 0.5rem;
  background-color: #fcfcfc;
  padding: 1rem;
  margin: 1rem;
}
.placeorder-info > div:first-child {
  margin-top: 0;
}
.placeorder-action > ul {
  padding: 0;
  list-style-type: none;
}
.placeorder-action > ul > li {
  display: flex;
  justify-content: space-between;
  margin-bottom: 1rem;
}
.placeorder-action > ul > li:last-child {
  font-size: 2rem;
  font-weight: bold;
  color: #c04000;
}
.placeorder-actions-payment > div {
  width: 100%;
}
.badge {
  background-color: #f02020;
  color: #ffffff;
  border-radius: 50%;
  padding: 0.2rem 0.7rem;
  font-size: 1.4rem;
  margin-left: 0.2rem;
}
.flex {
  display: flex;
}
.ml-4 {
  margin-left: 16px;
}
.card {
  border: 0.1rem #c0c0c0 solid;
  background-color: #f8f8f8;
  border-radius: 0.5rem;
  margin: 1rem;
}
.product {
  border: 0.1rem #c0c0c0 solid;
  margin: 1rem;
  border-radius: 0.5rem;
}
.card-body {
  padding: 1rem;
}
.card-body > * {
  margin-bottom: 0.5rem;
}
.price {
  font-size: 2rem;
}
/* Dropdown */
.dropdown {
  display: inline-block;
  position: relative;
}
/* admin */
.dropdown {
  display: inline-block;
  position: relative;
}
.form label {

  margin: 1rem 0;
}
.dropdown-content {
 position: absolute;
 display:none;
 right: 0;
 min-width: 12rem;
 padding: 1rem;
 z-index: 1;
 background-color:#203040 ;
 margin-top: 0.2rem;
 border-radius: 0.5rem;
}
.dropdown:hover .dropdown-content {
display: block;
} 
.loading {
  display: block !important;
}
.row {
  display: flex;
  flex-wrap: wrap;
  justify-content:space-between;
  align-items:center;
}
.row.top {
  align-items: flex-start;
}
.row.center {
  justify-content: center;

}
.row.start {
  justify-content:flex-start;
}

.col-1 {
  flex: 1 1 25rem;
}
.col-2 {
  flex: 2 1 50rem;
}
.col-3 {
  flex: 3 1 75rem;
}
.min-30 {
  min-width: 30rem;
}
.p-1 {
  padding: 1rem;  /* 16px*/
}
/* Carousal */
.carousel .slide img {
  max-width: 30rem;
  
}
.danger, .success { padding: 8px; color: #96613d; font-weight: 900;}
.danger { background: #fbdaef; }
.success {  background: #aff5b2; }
.text-success {color: #69c353;}
.text-danger {color: #f67e7e;}

img {
  border-radius: 0.5rem;
}
img.small {
  max-width: 5rem;
  width: 100%;
}
img.medium {
  width: 29rem;
  height: 29rem;
  /* width: 100%; */
}
img.large {
  width: 100%;
}
ul li {
   list-style-type: none;
}
/* Search */
.search button {
  border-radius: 0 0.5rem 0.5rem 0;
  border-right: none;
  margin-right: 0.5rem;
}
.search input {
  border-radius: 0.5rem 0 0 0.5rem;
  border-right: none;
  margin-left: 0.5rem;
}
.active {
  font-weight: bold;
}
 

.form-rate > div {
  max-width: 60rem;
  display: flex;
  flex-direction: column;
  margin: 1rem;
}
.form label {
  margin: 1rem 0;
}

/* Map */
.map {
  margin: -1rem;
  height: calc(100vh + 2rem);
}
.map-input-box {
  box-sizing: border-box;
  position: absolute;
  left: 0;
  right: 0;
  margin: 0.5rem auto;
  width: 25rem;
  height: 4rem;
  display: flex;
}
.map-input-box input {
  border-radius: 1rem 0 0 1rem;
  border-right: 0;
}
.map-input-box button {
  border-radius: 0 1rem 1rem 0;
  border-left: 0;
}

/** Pagination*/
.pagination a {
  padding: 1rem;
  margin: 0.5rem;
  border-radius: 0.5rem;
  border: 0.1rem #a4a4a4 solid;
  font-size: 1.6rem;
  font-family: Helvetica,Arial , sans-serif;
  --webkit-font-smoothing:antialiased;
  --moz-osx-font-smoothing:grayscale;
}
.pagination a.active {
  font-weight: bold;
}